/**
 * 
 * 
 */

import { useState, useEffect } from 'react';
import generateRGBA from '../lib/generateRGBA';

/**
 *  `useColorMap`
 * 
 * @param {Array} selectedPrefectures -  { prefCode: string, prefName: string } 
 *                                      [{prefCode:42, prefName:""}, , ,]
 * @returns {Object} colorMap - 
 *                              : { '01': 'rgba(255, 99, 132, 0.8)', '02': 'rgba(54, 162, 235, 0.8)', ... }
 */
const useColorMap = (selectedPrefectures) => {
    const [colorMap, setColorMap] = useState({});

    useEffect(() => {
        const newColorMap = { ...colorMap };

        selectedPrefectures.forEach(pref => {
            if (!newColorMap[pref.prefCode]) {
                newColorMap[pref.prefCode] = generateRGBA();
            }
        });

        setColorMap(newColorMap);
    }, [selectedPrefectures]);

    return colorMap;
};

export default useColorMap;
