/**
 * 
 * 
 * 
 * Chart.js
 * @see - https://www.chartjs.org/docs/latest/#chart-js
 */
const options = {
    scales: {
        y: {
            title: {
                display: true, // 
                text: '', // y
                font: {
                    size: 20, // 
                    weight: "bolder", // 
                },
            },
            rotation: 90,
        },
        x: {
            title: {
                display: true,
                text: '',
                font: {
                    size: 20
                },
            }
        }
    },
    /** animation @see - https://www.chartjs.org/docs/latest/configuration/animations.html#animations */
    animation: {
        duration: 1000, // 
        easing: 'easeInOutQuart', // 
        onComplete: function (animation) {
            const chart = animation.chart;
            const ctx = chart.ctx;
            ctx.setLineDash([]); // 
        },
        onProgress: function (animation) {
            const chart = animation.chart;
            const ctx = chart.ctx;
            if (animation.animationObject) {
                ctx.save();
                ctx.setLineDash([5, 15]);
                ctx.stroke();
                ctx.restore();
            }
        },
    },
    elements: {
        line: {
            tension: 0, // 0
        },
    },
};

export { options };
